<template>
    <div>
        <top-nav  mdl="wuliu" :select="1" ></top-nav>
        <div class="wyu-tips wyu-tips-warning">
            <p>运费模板 这里创建物流公司 </p>
        </div>
        <div class="wyu-main">
            <div class="wyu-block" v-loading="loading">
                <el-form label-width="80px" size="mini" v-model="searchForm" class="search-form" :inline="true">
                    <el-form-item label="公司名称" label-width="100">
                        <el-input v-model="searchForm.title"></el-input>
                    </el-form-item>
                    <div style="padding-left:80px;">
                        <el-button icon="el-icon-search" @click="getShippingList(1)" size="mini" type="primary">搜索</el-button>
                        <a href="javascript:void(0);" @click="searchForm.title = '',getShippingList(1)">取消搜索</a>
                        <el-button size="mini" @click="creatShippingEvent" type="danger">新增物流公司</el-button>
                    </div>
                </el-form>
                <el-table :data="datas.list" style="width: 100%" >
                    <el-table-column    prop="shipping_id" label="ID" width="80"></el-table-column>
                    <el-table-column    prop="shipping_code" label="物流编码" ></el-table-column>
                    <el-table-column    prop="shipping_name" label="物流名称" ></el-table-column>
                    <el-table-column    prop="shipping_desc" label="简单描述" ></el-table-column>
                    <!--<el-table-column    label="是否默认" >-->
                        <!--<template slot-scope="scope">-->
                            <!--<i style="font-size: 20px;" v-if="scope.row.is_default == 0" class="el-icon-circle-close-outline"></i>-->
                            <!--<i style="font-size: 20px;color: #409EFF" v-if="scope.row.is_default == 1" class="el-icon-circle-check-outline"></i>-->
                        <!--</template>-->
                    <!--</el-table-column>-->
                    <el-table-column label="操作"  width="200">
                        <template slot-scope="scope">
                            <!--<router-link :to="'/shipping/area/index/' + scope.row.shipping_id" style="color: #ffffff;margin: 0 10px;"><el-button size="mini" type="text">配置</el-button></router-link>-->
                           <el-button size="mini" @click="editShippingEvent(scope.row)" type="text">编辑</el-button>
                            <el-popover style="margin: 0 10px;" placement="top" width="160" v-model="scope.row.is_delete">
                                <p style="margin-bottom: 4px">确定删除吗？</p>
                                <div style="text-align: right; margin: 0">
                                    <el-button  :loading="loading" size="mini" type="text" @click="scope.row.is_delete = false">取消
                                    </el-button>
                                    <el-button :loading="loading" type="primary" size="mini" @click="del(scope.row)">确定
                                    </el-button>
                                </div>
                                <el-button type="text" slot="reference">删除</el-button>
                            </el-popover>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="page">
                    <div class="page-mian">
                        <el-pagination @current-change="getShippingList" :page-size="datas.limit"
                                       layout="total, prev, pager, next" :total="datas.count">
                        </el-pagination>
                    </div>
                </div>
            </div>
        </div>

        <!--编辑新增 弹窗-->
        <el-dialog title="物流公司" v-loading="subLoading" :visible.sync="dialog" width="500px" :modal-append-to-body="false" center>
            <el-form :model="form" style="padding: 0" size="small" label-width="80px">
                <el-form-item label="物流编码" >
                    <el-input v-model="form.shipping_code" autocomplete="off"></el-input>
                    <p class="wyu-tips">一般为物流公司首拼。<a style="color: #1593ff" href="http://www.kdniao.com/file/2019%E5%BF%AB%E9%80%92%E9%B8%9F%E6%8E%A5%E5%8F%A3%E6%94%AF%E6%8C%81%E5%BF%AB%E9%80%92%E5%85%AC%E5%8F%B8%E7%BC%96%E7%A0%81.xlsx" target="_blank">参考查询编码</a></p>
                </el-form-item>
                <el-form-item  label="公司名称" >
                    <el-input v-model="form.shipping_name" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item  label="简短描述" >
                    <el-input v-model="form.shipping_desc" autocomplete="off"></el-input>
                </el-form-item>
                <!--<el-form-item  label="是否启用" >-->
                    <!--<el-switch-->
                            <!--v-model="form.enabled"-->
                            <!--:active-value="1"-->
                            <!--:inactive-value="0"-->
                            <!--&gt;-->
                    <!--</el-switch>-->
                <!--</el-form-item>-->
                <!--<el-form-item  label="是否默认" >-->
                    <!--<el-switch-->
                            <!--v-model="form.is_default"-->
                            <!--:active-value="1"-->
                            <!--:inactive-value="0"-->
                            <!--&gt;-->
                    <!--</el-switch>-->
                <!--</el-form-item>-->
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button size="small" @click="dialog = false">取 消</el-button>
                <el-button size="small" type="primary" @click="creatShipping(true)">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import TopNav from "@/components/TopNav";
    import * as api from "@/api";

    export default {
        name: 'ShippingIndex',
        components: {TopNav},
        data() {
            return {
                //修改 添加 弹窗  菊花
                subLoading: false,
                //整体列表 菊花
                loading: false,
                //列表数据 包含 list count limit
                datas: [],
                //数据
                form:this.initForm(),
                //修改 添加 状态控制
                dialog: false,
                isUpdate:false,
                //搜索 form
                searchForm: {
                    title: '',  // 物流公司标题
                },

            }
        },
        created() {
            this.getShippingList();
        },
        methods: {
            //初始化form数据
            initForm(){
                return {
                    shipping_code:'', //物流编码
                    shipping_name:'', //物流名称
                    shipping_desc:'', //简单描述
                    enabled:0,        //是否启用
                    is_default:0,     //是否默认
                }
            },
            //新增物流模板事件
            creatShippingEvent(){
                this.isUpdate = false;
                this.form =  this.initForm();
                this.dialog = true;
            },

            //修改物流模板
            editShippingEvent(row){
                this.isUpdate = true;
                this.form =  row;
                this.dialog = true;
            },

            //新增物流
            creatShipping(){
                this.subLoading = true;
                let act = this.isUpdate ? 'edit' : 'create';
                api.post("/shop/admin.shipping/" +act ,{
                    appToken: this.$store.getters.appToken,
                    shipping_code:this.form.shipping_code,
                    shipping_name:this.form.shipping_name,
                    shipping_desc:this.form.shipping_desc,
                    enabled:this.form.enabled,
                    is_default:this.form.is_default,
                    shipping_id:this.form.shipping_id,
                }).then(res => {
                    this.subLoading = false;
                    this.form = this.initForm();
                    this.$message({
                        message: '恭喜你，操作成功',
                        type: 'success'
                    });
                    this.dialog = false;
                    this.getShippingList();
                }).catch((error) => {
                    this.subLoading = false;
                });
            },

            //获取物流公司
            getShippingList(page = 1){
                this.loading =true;
                api.post("/shop/admin.shipping/index", {
                    appToken: this.$store.getters.appToken,
                    page:page,
                    shipping_name:this.searchForm.title,
                }).then(res => {
                    this.loading =false;
                    this.datas = res;
                }).catch((error) => {
                    this.loading =false;
                });
            },


           //删除快递公司
            del(row) {
                this.loading = true;
                api.post("/shop/admin.shipping/del", {
                    appToken: this.$store.getters.appToken,
                    shipping_id:row.shipping_id
                }).then(res => {
                    this.loading = false;
                    this.$message({
                        message: '恭喜你，操作成功',
                        type: 'success',
                    });
                    this.getShippingList();
                }).catch((error) => {
                    this.loading = false;
                });
            },

        }
    }
</script>

